@import url("https://fonts.googleapis.com/css2?family=Asap&family=Roboto:ital,wght@0,500;0,900;1,500&display=swap");
@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,300,700);

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.body {
  background-color:  white;
  font-family: "Courier Prime", monospace;
}

.title {
  text-align: center;
  padding-left: 120px;
  padding-right: 120px;
  padding-top: 80px;
  padding-bottom: 40px;
}

body {
  margin: 0px;
  padding: 0px;
  font-family: "Asap", sans-serif;
  background-color: #fdfdfe;
}

header {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
}

header h1 {
  position: absolute;
  width: 90%;
  font-size:60px;
  font-weight: 600;
  color: transparent;
  background-image: linear-gradient(to right ,#553c9a, #ee4b2b, #00c2cb, #ff7f50, #553c9a);
  -webkit-background-clip: text;
  background-clip: text;    
  background-size: 200%;
  background-position: -200%;
  transition: all ease-in-out 2s;
  cursor: pointer;
}

header h1:hover{
  background-position: 200%;
}

.player {
  
  padding-left: 80px;
  padding-right: 50px;
  padding-top: 50px;
  padding-bottom: 300px;
  text-align: center;
}

.ranking {
  padding-bottom: 20px;
  text-align: center;
  font-size: 50px;
}

.ranking h3 {
  font-weight: 900;
}

.team {
  padding-top: 35px;
  padding-bottom: 35px;
  text-align: center;
}

.explanation {
  
  margin: auto;
  width: 60%;
  border: 3px solid #73AD21;
  padding: 40px;
  text-align: left;
  
}

@media (max-width: 800px) {
  
  .body {
    background-color:  white;
    font-family: "Courier Prime", monospace;
  }
  
  .title {
    text-align: center;
    padding-left: 10%;
    padding-right: 10%;
    padding-top: 10%;
    padding-bottom: 10%;
  }
  
  img {
    width: 80%;
    height: auto;
  }
  
  body {
    margin: 0px;
    padding: 0px;
    font-size: 20px;
  }
  
  header {
    width: 100%;
    height: 100vh;
  }
  
  header h1 {
    font-size: 20px;
  }
  
  header h1:hover{
    background-position: 200%;
  }
  
  .player {
    
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 100px;
  }
  
  .ranking {
    padding-bottom: 10px;
    font-size: 15px;
  }
  
  .ranking h3 {
    font-weight: 900;
    font-size: 15px;
  }
  
  .team {
    padding-top: 15px;
    padding-bottom: 15px;
    font-size: 15px;
  }
  
  .explanation {
    
    margin: auto;
    width: 60%;
    border: 3px solid #73AD21;
    padding: 10px;
    font-size: 15px;
    
  }
  
}




* {
  font-family: Roboto;
}

section {
  width: 100%;
  display: inline-block;
  background: #ccc;
  height: 60vh;
  text-align: center;
  font-size: 22px;
  font-weight: 700;
  text-decoration: underline;
}

.footer-distributed {
  background-color: #292c2f;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  font: normal 16px sans-serif;
  padding: 45px 50px;
}

.footer-distributed .footer-left p {
  color: #8f9296;
  font-size: 14px;
  margin: 0;
}
/* Footer links */

.footer-distributed p.footer-links {
  font-size: 18px;
  font-weight: bold;
  color: #ffffff;
  margin: 0 0 10px;
  padding: 0;
  transition: ease .25s;
}

.footer-distributed p.footer-links a {
  display: inline-block;
  line-height: 1.8;
  text-decoration: none;
  color: inherit;
  transition: ease .25s;
}

.footer-distributed .footer-links a:before {
  content: "Â·";
  font-size: 20px;
  left: 0;
  color: #fff;
  display: inline-block;
  padding-right: 5px;
}

.footer-distributed .footer-links .link-1:before {
  content: none;
}

.footer-distributed .footer-right {
  float: right;
  margin-top: 6px;
  max-width: 180px;
}

.footer-distributed .footer-right a {
  display: inline-block;
  width: 35px;
  height: 35px;
  background-color: #33383b;
  border-radius: 2px;
  font-size: 20px;
  color: #ffffff;
  text-align: center;
  line-height: 35px;
  margin-left: 3px;
  transition:all .25s;
}

.footer-distributed .footer-right a:hover{transform:scale(1.1); -webkit-transform:scale(1.1);}

.footer-distributed p.footer-links a:hover{text-decoration:underline;}

/* Media Queries */

@media (max-width: 600px) {
  .footer-distributed .footer-left, .footer-distributed .footer-right {
    text-align: center;
  }
  .footer-distributed .footer-right {
    float: none;
    margin: 0 auto 20px;
  }
  .footer-distributed .footer-left p.footer-links {
    line-height: 1.8;
  }
}








